import {
    Text,
    Image,
    Flex,
    Link,
    Grid,
    GridItem,
  } from '@chakra-ui/react';
import Fade from 'react-reveal/Fade';
import avatarIg from '../assets/images/avatar_ig.png';
import avatarFb from '../assets/images/avatar_fb.png';
import avatarIgEn from '../assets/images/avatar_ig_en.png';
import avatarIgDaily from '../assets/images/avatar_ig_daily.png';
import avatarYt from '../assets/images/avatar_yt.png';

const Sectionlink = () => (
    <Grid templateColumns={["repeat(4, 1fr)", "repeat(4, 1fr)","repeat(8, 1fr)", "repeat(12, 1fr)"]} gap={4} my={10} px="1em">
        <GridItem position="relative" colSpan={2}>
            <Fade bottom>
                <Flex W="100%" paddingBottom="100%" bgColor="#6DB6D7" borderRadius="8px">
                    <Link href="https://www.instagram.com/shikamaro_painting/">
                        <Flex position="absolute" w="100%" top="0" bottom="0" align="center" justifyContent="center" flexDir="column">
                            <Image h="50px" w="50pX" fit="cover" src={avatarIg} borderRadius="30px" alt="シロマロ" />
                            <Text  fontSize="20px" color="#fff" >Instagram</Text>
                            <Text  fontSize="20px" color="#fff" >シカマロ</Text>
                        </Flex>
                    </Link>
                </Flex>
            </Fade>
        </GridItem>
        <GridItem position="relative" colSpan={2}>
            <Fade bottom>
                <Flex W="100%" paddingBottom="100%" bgColor="#A3B0B7" borderRadius="8px">
                    <Link href="https://www.facebook.com/shikamaropainting">
                        <Flex position="absolute" w="100%" top="0" bottom="0" align="center" justifyContent="center" flexDir="column">
                            <Image h="60px" w="60PX" fit="cover" src={avatarFb} borderRadius="30px" alt="シロマロ" />
                            <Text  fontSize="20px" color="#fff" >Facebook</Text>
                            <Text  fontSize="20px" color="#fff" >小白的日常</Text>
                        </Flex>
                    </Link>
                </Flex>
            </Fade>
        </GridItem>
        <GridItem position="relative" colSpan={2}>
            <Fade bottom>
                <Flex W="100%" paddingBottom="100%" bgColor="#A3B0B7" borderRadius="8px">
                    <Link href="https://www.instagram.com/shikamaro_painting_en/">
                        <Flex position="absolute" w="100%" top="0" bottom="0" align="center" justifyContent="center" flexDir="column">
                            <Image h="60px" w="60PX" fit="cover" src={avatarIgEn} borderRadius="30px" alt="シロマロ" />
                            <Text  fontSize="20px" color="#fff" >Instagram</Text>
                            <Text  fontSize="20px" color="#fff" >English ver</Text>
                        </Flex>
                    </Link>
                </Flex>
            </Fade>
        </GridItem>
        <GridItem position="relative" colSpan={2}>
            <Fade bottom>
                <Flex W="100%" paddingBottom="100%" bgColor="#6DB6D7" borderRadius="8px">
                    <Flex position="absolute" w="100%" top="0" bottom="0" align="center" justifyContent="center" flexDir="column">
                        <Image h="60px" w="60PX" fit="cover" src={avatarIg} borderRadius="22px" alt="シロマロ" />
                        <Text  fontSize="20px" color="#fff" >Clubhouse</Text>
                        <Text  fontSize="20px" color="#fff" >@shiromaro</Text>
                    </Flex>
                </Flex>
            </Fade>
        </GridItem>
        <GridItem position="relative" colSpan={2}>
            <Fade bottom>
                <Flex W="100%" paddingBottom="100%" bgColor="#6DB6D7" borderRadius="8px">
                    <Link href="https://www.instagram.com/shikamaro0125/">
                        <Flex position="absolute" w="100%" top="0" bottom="0" align="center" justifyContent="center" flexDir="column">
                            <Image h="60px" w="60PX" fit="cover" src={avatarIgDaily} borderRadius="30px" alt="シロマロ" />
                            <Text  fontSize="20px" color="#fff" >Instagram</Text>
                            <Text  fontSize="20px" color="#fff" >小白的日常</Text>
                        </Flex>
                    </Link>
                 </Flex>
            </Fade>
        </GridItem>
        <GridItem position="relative" colSpan={2}>
            <Fade bottom>
                <Flex W="100%" paddingBottom="100%" bgColor="#A3B0B7" borderRadius="8px">
                    <Link href="https://www.youtube.com/channel/UCpabpXwMWNfB31IQ8p9KGlw">
                        <Flex position="absolute" w="100%" top="0" bottom="0" align="center" justifyContent="center" flexDir="column">
                            <Image h="60px" w="60PX" fit="cover" src={avatarYt} borderRadius="30px" alt="シロマロ" />
                            <Text  fontSize="20px" color="#fff" >Youtube</Text>
                            <Text  fontSize="20px" color="#fff" >シカマロ</Text>
                        </Flex>
                    </Link>
                </Flex>
            </Fade>
        </GridItem>
    </Grid>
  );
  
  export default Sectionlink;
  