import {
    Box,
    // Text,
    Image,
    Flex,
  } from '@chakra-ui/react';

import bg from '../assets/images/hero_bg.png';
import kumo1 from '../assets/images/kumo1.png';
import bgLg from '../assets/images/hero_bg_lg.png';
import { Parallax  } from 'react-scroll-parallax';

const Footer = () => (
    <Flex align="center" width="100%" overflow="hidden" height="500px">
        <Parallax className="custom-class" y={[-30, 50]} tagOuter="figure">
            <Box
                // bgImage="url('/shikamaro/static/media/hero_bg.912c6fe9.png')"
                bgImage={[`url('${bg}')`,`url('${bg}')`,`url('${bgLg}')`]}
                bgRepeat="no-repeat"
                w="100vw" h="500px "
                position="relative"
                backgroundSize="cover"
            >
                <Parallax x={[100, -80]} y={[ 0, 160]} tagOuter="figure">
                <Image marginTop="100px" w="300px" height="100px" fit="cover" src={kumo1} opacity=".75" alt="シロマロ" />
                </Parallax>
                <Parallax x={[0, 50]} y={[10, 140]} tagOuter="figure">
                    <Image marginTop="100px" w="180px" height="90px" fit="cover" src={kumo1}  alt="シロマロ" />
                </Parallax>
            </Box>
        </Parallax>
    </Flex>
  );
  
  export default Footer;
  